.back-popup {
    position: absolute;
    z-index: 1000;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.58);
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup {
    background: $color-1;
    padding: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 10px;
    font-family: $font-1;
    font-size: 4vw;
    border-radius: 30px;
    border: 6px solid rgb(242, 239, 239);

    h4,
    p {
        -webkit-text-stroke: 0.1px rgb(242, 239, 239);
        color: $color-1;
    }

    p {
        font-weight: bold;
    }

    button {
        padding: 10px 50px;
        font-family: $font-1;
        font-weight: bold;
        border-radius: 40px;
        font-size: 3vw;
        color: $color-1;
        transition: 0.3s ease-in-out;
        border: 2px solid $color-2;

        &:hover {
            color: $color-2;
            background: $color-1;
            border: 2px solid $color-2;
            transform: scale(1.1);
        }
    }
}

@media screen and (max-width: 900px) {
    .popup {
        h4,
        p {
            color: wheat;
            font-weight: 500;
            font-size: 1.5rem;
            -webkit-text-stroke:none;
        }
    }
}
